.deals,
.home {
  position: relative;
}
.deals_bg {
  position: absolute;
  background: url(../images/themes/deals_bg.png) center center no-repeat;
  background-size: cover;
  opacity: 0.5;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.home_bg {
  position: absolute;
  background: url(../images/themes/platform_bg.png) center center no-repeat;
  background-size: cover;
  opacity: 0.5;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.results-platform .header {
  width: 100%;
  max-width: 1300px;
  background: #060026;
  border-radius: 13px;
  margin: 0 auto;
}
.results-platform #app {
  grid-template-areas: "header header" "aside main" "footer footer";
  grid-template-rows: minmax(80px, 90px) auto auto;
  padding: 26px 15px 0 15px;
  max-width: 1330px;
  position: relative;
  z-index: 10;
}
.results-platform .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.results-platform .header-nav__item {
  margin-left: 0;
}
.results-platform .main {
  padding-top: 60px;
  margin-bottom: 100px;
  background: transparent;
}
.wrapper {
  max-width: 1140px;
  margin: 0 auto;
}
.platform-heading {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 42px;
  text-transform: uppercase;
  color: #E1DFEC;
  margin-bottom: 46px;
}
@media (max-width: 992px) {
  .results-platform #app {
    grid-template-columns: unset;
  }
  .results-platform .header-bignav {
    display: flex;
    margin-right: 50px;
  }
}
@media (max-width: 524px) {
  .results-platform .header-nav__item_date {
    display: none;
  }
}
.results-platform .aside {
  background: #060026;
  border-radius: 13px;
  max-width: 200px;
  max-height: 528px;
  padding: 34px 32px 30px 32px;
  margin-top: 120px;
}
@media (max-width: 992px) {
  .results-platform .aside {
    margin-top: 0px;
  }
}
.platforn-nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.platforn-nav__actions {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}
.platforn-nav__linck {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 18px;
  color: #fff;
  padding: 14px 38px;
  border-radius: 8px;
  transition: all 0.3s ease-in;
}
.platforn-nav__linck_white {
  border: 1px solid #FFFFFF;
}
.platforn-nav__linck_white:hover {
  background: #fff;
  color: #060026;
  text-decoration: none;
}
.platforn-nav__linck_pinck {
  background: #EB00FF;
  border: 1px solid #EB00FF;
}
.platforn-nav__linck_pinck:hover {
  background: transparent;
  color: #fff;
  text-decoration: none;
  border: 1px solid #FFFFFF;
}
.platforn-nav__help {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 110.5%;
  color: #EB00FF;
  transition: all 0.3s ease-in;
}
.platforn-nav__help span {
  text-decoration: underline;
  margin-right: 6px;
}
.platforn-nav__help svg {
  fill: #EB00FF;
  transition: all 0.3s ease-in;
}
.platforn-nav__help:hover {
  color: #fff;
}
.platforn-nav__help:hover svg {
  fill: #FFFFFF;
}
@media (max-width: 992px) {
  .results-platform .aside {
    transform: translateX(-120%);
    height: 400px;
  }
  .results-platform .aside.naw-show {
    transform: translateX(0);
  }
}
.results-platform .footer {
  padding: 30px 20px;
  background: transparent;
}
.results-platform .footer__content {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  color: #E1DFEC;
  margin-bottom: 0;
}
.results-platform .footer__content span {
  display: inline-block;
  margin: 0 auto;
}
.deals-cerds {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
}
.deals-cerds__item {
  width: calc(25% - 20px);
  position: relative;
  background: #1B0E6D;
  border-radius: 13px;
  border: 2px solid #4fd84e;
  color: #FFFFFF;
  padding: 22px 20px 26px 20px;
  margin: 18px 10px;
}
.deals-cerds__item.loss {
  border: 2px solid #d2393b;
}
.deals-cerds__item.loss .deals-cerds__label {
  background: linear-gradient(151.66deg, #FA8F8F 2.49%, #D33A3C 103.62%);
}
@media (max-width: 1180px) {
  .deals-cerds__item {
    width: calc(100% / 3 - 20px);
  }
}
@media (max-width: 674px) {
  .deals-cerds__item {
    width: calc(100% / 2 - 20px);
  }
}
@media (max-width: 470px) {
  .deals-cerds__item {
    width: calc(100% - 20px);
  }
}
.deals-cerds__label {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: center;
  padding-top: 4px;
  position: absolute;
  width: 72px;
  height: 25px;
  background: linear-gradient(151.66deg, #9AFFCF 2.49%, rgba(67, 215, 54, 0.97) 103.62%);
  border-radius: 13px;
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
}
.deals-cerds__heading {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 7px;
}
.deals-cerds__row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px dashed #FFFFFF;
  padding-top: 8px;
  padding-bottom: 8px;
}
.deals-cerds__tecker {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
}
.deals-cerds__value {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  text-align: right;
}
.deals-cerds__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 8px;
  padding-bottom: 8px;
}
.cerds-actions {
  text-align: center;
  margin-top: 40px;
}
.cerds-actions__linck {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 18px;
  color: #fff;
  padding: 4px 20px;
  opacity: 0.8;
  border: 1px solid #FFFFFF;
  border-radius: 13px;
  transition: all 0.3s ease-in;
}
.cerds-actions__linck:hover {
  background: #fff;
  color: #060026;
  text-decoration: none;
  opacity: 1;
}
.results {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  gap: 20px;
}
.results__item {
  width: calc(50% - 10px);
  padding: 28px;
  border-radius: 10px;
  color: #fff;
}
.results__title {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 27px;
}
.results__item_pinck {
  display: flex;
  align-items: center;
  background: linear-gradient(277.03deg, #EB00FF 3.58%, rgba(147, 129, 255, 0.75) 96.52%);
}
.results__block {
  flex: 1 1 auto;
}
.results__item_blue {
  display: flex;
  flex-direction: column;
  align-items: start;
  position: relative;
  background: linear-gradient(105.87deg, #A08EFF -1.69%, #2F0DFF 99.77%);
}
.results__item_blue::before {
  content: '';
  position: absolute;
  width: 37px;
  height: 35px;
  top: 34px;
  right: 40px;
  background: url(../images/card/shape.png) center center no-repeat;
  background-size: cover;
}
.results__item_blue .results__block {
  max-width: 284px;
}
.results__list {
  padding-left: 20px;
  margin-top: 20px;
  margin-bottom: 26px;
}
.results__summ {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 54px;
}
.results__summ span {
  text-transform: uppercase;
}
.results__img {
  flex: 1 1 185px;
}
.results__img img {
  width: 100%;
  height: auto;
}
.results__btn {
  padding: 4px 20px;
  opacity: 0.8;
  border: 1px solid #FFFFFF;
  border-radius: 13px;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 36px;
  color: #fff;
  background: transparent;
  transition: all 0.3s ease-in;
}
.results__btn:hover {
  background: #fff;
  color: #060026;
  text-decoration: none;
  opacity: 1;
}
.results__btn ._select {
  background: #fff;
  color: #060026;
  text-decoration: none;
  opacity: 1;
}
.results__item_grean {
  position: relative;
  background: linear-gradient(277.03deg, #88FF3F 3.58%, rgba(199, 252, 167, 0.78) 96.52%);
}
.results__item_grean::before {
  content: '';
  position: absolute;
  width: 37px;
  height: 35px;
  top: 34px;
  right: 40px;
  background: url(../images/card/calendar.png) center center no-repeat;
  background-size: cover;
}
.results__item_grean .results__title {
  max-width: 200px;
  margin-bottom: 16px;
}
.results__form {
  display: flex;
  align-items: center;
}
.results__form-group {
  display: flex;
  flex-direction: column;
}
.results__form-group label {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 110.5%;
  color: #fff;
  margin-bottom: 7px;
}
.results__form-group input,
.results__form-group select {
  width: 100%;
  padding: 4px 20px;
  opacity: 0.8;
  border: 1px solid #FFFFFF;
  border-radius: 13px;
}
.results__gilter-btn {
  display: flex;
  gap: 20px;
  margin-bottom: 18px;
}
.results__gilter-btn .results__btn {
  font-size: 16px;
  line-height: 30px;
  padding: 4px 20px;
}
.results__item_sky {
  position: relative;
  max-height: 200px;
  background: linear-gradient(105.87deg, #8EA0FF -1.69%, #0D99FF 99.77%);
}
.results__item_sky::before {
  content: '';
  position: absolute;
  width: 37px;
  height: 35px;
  top: 34px;
  right: 40px;
  background: url(../images/card/shield-cross.png) center center no-repeat;
  background-size: cover;
}
.results__item_sky .results__list {
  margin-bottom: 0;
}
